import React, { useState, useEffect, useRef } from 'react';
import { useSpring, useTransition, animated, config, useChain } from 'react-spring';

import Event from './Event';

// const pages = [
//     ({ style, arrowClicked }) => <animated.div style={style} className="relative">
//         <Event
//             dayNum={0}
//             arrowClicked={arrowClicked} />
//     </animated.div>,
//     ({ style, arrowClicked }) => <animated.div style={style} className="relative">
//         <Event
//             dayNum={1}
//             arrowClicked={arrowClicked} />
//     </animated.div>,
//     ({ style, arrowClicked }) => <animated.div style={style} className="relative">
//         <Event
//             dayNum={2}
//             arrowClicked={arrowClicked} />
//     </animated.div>,
// ];

const Events = () => {

    const [eventDayOpened, setEventDayOpened] = useState(-1);

    const transRef1 = useRef()
    const transRef2 = useRef()
    const transRef3 = useRef()

    useChain(eventDayOpened == 0 ? [transRef2, transRef3, transRef1] : eventDayOpened == 1 ? [transRef1, transRef3, transRef2] : [transRef2, transRef1, transRef3], [0, 0, .4]);

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');

    const onNameChange = (event) => {
        let name = event.target.value;
        if (name.length > 1) {
            setName(name);
        } else {
            setName('');
        }
    }

    const onEmailChange = (event) => {
        let email = event.target.value;
        let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(email)) {
            setEmail(email);
        } else {
            setEmail('');
        }
    }

    let firstDayStyle = 'events-header pointer';
    if (eventDayOpened === 0) {
        firstDayStyle = 'events-header grad-bg pointer white';
    }
    let secondDayStyle = 'events-header pointer';
    if (eventDayOpened === 1) {
        secondDayStyle = 'events-header grad-bg pointer white';
    }
    let thirdDayStyle = 'events-header pointer';
    if (eventDayOpened === 2) {
        thirdDayStyle = 'events-header grad-bg pointer white';
    }

    return (
    <div className="relative overflow-hidden full-width">
        <div className="input-fields">
                <div style={{marginTop: 10}} className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onNameChange} required/>
                    <label>Как вас зовут?</label>
                </div>

                <div className="flex-column relative input-margin-bottom">
                    <input type="text" className="form-inputs-width" onChange={onEmailChange} required/>
                    <label>Электронная почта</label>
                </div>
        </div>
        <h1 className="text-centered">Выберите день</h1>

        <div className="flex ev-cards">
            <div className={firstDayStyle} onClick={() => setEventDayOpened(eventDayOpened == 0 ? -1 : 0)} >
                <h2 className="text-centered">Открытие</h2>
                <p className="text-centered">Воскресенье<br />27 Октября 2019</p>
            </div>
            <div className={secondDayStyle} onClick={() => setEventDayOpened(eventDayOpened == 1 ? -1 : 1)} >
                <h2 className="text-centered">Первый день</h2>
                <p className="text-centered">Суббота<br />3 Ноября 2019</p>
            </div>
            <div className={thirdDayStyle} onClick={() => setEventDayOpened(eventDayOpened == 2 ? -1 : 2)} >
                <h2 className="text-centered">Последний день</h2>
                <p className="text-centered">Воскресенье<br />3 Ноября 2019</p>
            </div>
        </div>

        <Event dayNum={0} eventDayOpened={eventDayOpened} transRef={transRef1} />
        <Event dayNum={1} eventDayOpened={eventDayOpened} transRef={transRef2} />
        <Event dayNum={2} eventDayOpened={eventDayOpened} transRef={transRef3} />
    </div>
    );
}

export default Events;